const BASE_URL = "http://localhost:3000";

async function fetchWithAddress(url = '', config ={}){
    const responce = await fetch(url,config);
    return responce.ok
    ? await responce.json()
    : Promise.reject(new Error('Not found'));
}

export function fetchAuthors() {
    return fetchWithAddress(`${BASE_URL}/authors?_embed=books`)
}
export function fetchBooks() {
    return fetchWithAddress(`${BASE_URL}/books`)
}
export function fetchBookId() {
    return fetchWithAddress(`${BASE_URL}/books/?_expand=author`)
}